@import url(https://fonts.googleapis.com/css2?family=Bitter:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&display=swap);
html, body {
  position: absolute;
  padding: 0;
  margin: 0;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: hsla(193, 46%, 77%, 1);
  background: linear-gradient(45deg, hsla(193, 46%, 77%, 1) 0%, hsla(153, 42%, 77%, 35%) 45%, hsla(104, 36%, 77%, 1) 100%);
}
* {
  font-family: 'Bitter', serif;
  letter-spacing: 1.2px;
  box-sizing: border-box;
}
@-webkit-keyframes drop-pin-rise {
  0% {
    bottom: 0;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes drop-pin-rise {
  0% {
    bottom: 0;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.drop-pin-button {
  -webkit-animation: drop-pin-rise 0.5s;
          animation: drop-pin-rise 0.5s;
  z-index: 10;
  cursor: pointer;
  position: fixed;
  right: 1.5rem;
  bottom: 1.5rem;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 1.25rem;
  border: 1px solid black;
  background-color: lightgreen;
  box-shadow: 1px 1px 3px gray;
  transition: 0.3s;
}
.drop-pin-button img {
  width: 100%;
}
.drop-pin-backdrop {
  z-index: 7;
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: #ccccccaa;
}
.fire-button {
  -webkit-animation: drop-pin-rise 0.5s;
          animation: drop-pin-rise 0.5s;
  z-index: 10;
  cursor: pointer;
  position: fixed;
  right: 1.5rem;
  bottom: 1.5rem;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 1.25rem;
  border: 1px solid black;
  background-color: rgb(226, 104, 104);
  box-shadow: 1px 1px 3px white;
  transition: 0.3s;
  display: grid;
  align-items: center;
  justify-items: center;
}
.fire-button img {
  width: 1.5rem;
}
.fire-button.one {
  bottom: 4.5rem;
}
.fire-button.two {
  bottom: 7.5rem;
  width: 5rem;
  grid-template-columns: 1fr 1fr;
}
.fire-button.three {
  bottom: 10.5rem;
  width: 7.5rem;
  grid-template-columns: 1fr 1fr 1fr;
}
.fire-button.four {
  bottom: 13.5rem;
  width: 10rem;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.fire-button.five {
  bottom: 16.5rem;
  width: 12.5rem;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}
.drop-pin.place-list .exit {
  transition: 0.3s;
  position: fixed;
  right: 1rem;
  top: 1rem;
  width: 2.5rem;
  height: 2.5rem;
  padding: 0.5rem;
  border-radius: 1.25rem;
  background-color: #ffffff;
  box-shadow: 1px 1px 3px #666;
  cursor: pointer;
}
.drop-pin.place-list .exit img {
  width: 100%;
}
.drop-pin.place-list button {
  cursor: pointer;
  font-size: 1rem;
  display: block;
  width: 12ch;
  padding: .5rem 0.25rem;
  text-align: center;
  margin: 1rem auto;
  background-color: #ffffff;
  border: none;
  border-radius: 0.25rem;
  box-shadow: 1px 1px 3px #666;
}
.drop-pin.place-list hr {
  margin: 1.25rem 0 0.75rem 0;
}
.drop-pin.place-list h3 {
  font-size: 1rem;
  font-weight: 400;
  margin: 0.25rem;
}
.drop-pin.place-list .select-dropdown-custom-place label {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1rem;
}
.drop-pin.place-list .select-dropdown-custom-place input {
  outline: none;
  border: none;
  font-size: 1rem;
}
.drop-pin.place-list .select-dropdown-custom-place img {
  max-width: 100%;
}
.drop-pin.place-list .select {
  font-weight: 200;
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1rem;
  width: 55ch;
  max-width: 84vw;
  padding: 1rem 2.5ch;
  background-color: #ffffff;
  box-shadow: 1px 1px 3px #666;
  margin: 0 auto;
}
.drop-pin.place-list .select.open {
  border-bottom: 1px solid #333;
}
.drop-pin.place-list .select img {
  transition: 0.3s;
  width: 100%;
}
.drop-pin.place-list .select.open img {
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}
.drop-pin.place-list .select + .select-dropdown {
  font-weight: 200;
  transition: 0.3s;
  -webkit-transform-origin: top;
          transform-origin: top;
  background-color: #ffffff;
  width: 55ch;
  max-width: 84vw;
  max-height: 67vh;
  overflow-y: scroll;
  padding: 1rem 2.5ch;
  margin: 0 auto;
  box-shadow: 1px 1px 3px #666;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
}
.drop-pin.place-list .select.open + .select-dropdown {
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
  color: hsla(0, 0, 0, 1);
}
.drop-pin.place-list .select + .select-dropdown div {
  transition: 0.3s;
  transition-delay: 0.2s;
  color: hsla(0, 0%, 0%, 0);
  padding: 0.125rem 0;
  margin: 0.25rem 0;
}
.drop-pin.place-list .select.open + .select-dropdown div {
  color: hsla(0, 0%, 0%, 1);
}
.drop-pin.place-list header {
  text-align: center;
  max-width: 75vw;
  margin: auto;
  text-decoration: underline;
}
.drop-pin.place-list h1 {
  color: hsla(120, 73%, 5%, 1);
}
@media (hover: hover) {
  .drop-pin-button:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 2px 2px 7px gray;
  }
  .fire-button:hover {
    box-shadow: 2px 2px 7px gray;
  }
  .drop-pin.place:hover {
    background-color: hsla(120, 73%, 15%, 1);
    box-shadow: 2px 2px 5px hsla(120, 73%, 15%, 0.4);
  }
  .drop-pin.place-list .exit:hover {
    box-shadow: 2px 2px 5px #666;
  }
  .drop-pin.place-list .select.open + .select-dropdown div:hover {
    text-decoration: underline;
  }
}
@-webkit-keyframes ol-map-fade-in {
  0% {
    opacity: 0;
  }
}
@keyframes ol-map-fade-in {
  0% {
    opacity: 0;
  }
}
#ol-map {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  -webkit-animation: ol-map-fade-in 2s;
          animation: ol-map-fade-in 2s;
}
#ol-map.hidden {
  visibility: hidden;
}
.auth-conatiner {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  padding: 2rem;
  width: 84vw;
  max-width: 60ch;
  margin: 10vh auto;
  background-color: hsla(0, 0%, 100%, 0.6);
  border-radius: .5rem;
  border-bottom: 0.25rem solid hsla(0, 0%, 100%, 0.1);
  border-right: 0.25rem solid hsla(0, 0%, 100%, 0.1);
  border-top: 0.25rem solid hsla(0, 0%, 100%, 0.8);
  border-left: 0.25rem solid hsla(0, 0%, 100%, 0.8);
}
.auth-conatiner input {
  font-size: 0.95rem;
  height: 1.75rem;
  width: 18ch;
  padding: 0.25rem 2ch;
  outline: none;
  background-color: transparent;
  border: none;
  border-bottom: 0.125rem solid hsla(193, 46%, 77%, 1);
}
.auth-conatiner button {
  cursor: pointer;
  font-size: 0.95rem;
  padding: 0.25rem 2ch;
  margin-block: 0.5rem;
  background-color: hsla(193, 46%, 77%, 0.3);
  border: 0.025rem solid hsla(193, 77%, 5%, 1);
  border-radius: 0.25rem;
}
@media (hover: hover) {
  .auth-conatiner button {
    transition: 0.3s;
  }
  .auth-conatiner button:hover {
    font-weight: 600;
    color: hsla(193, 46%, 77%, 1);
    background-color: hsla(193, 77%, 5%, 1);
  }
}
@media (hover: none) {
  .auth-conatiner button:active {
    font-weight: 600;
    color: hsla(193, 46%, 77%, 1);
    background-color: hsla(193, 77%, 5%, 1);
  }
}

